"use client";

import { useState } from "react";
import AlertaStock from "../../components/almacen/AlertaStock";
import EntradasSalidas from "../../components/almacen/EntradasSalidas";
import KardexHistorial from "../../components/almacen/KardexHistorial";

export default function GestionAlmacenPage() {
  const [vistaActiva, setVistaActiva] = useState<"alerta" | "movimientos" | "kardex">("alerta");

  const cambiarVista = (vista: typeof vistaActiva) => setVistaActiva(vista);

  return (
    <main className="ventas-layout">
      <div className="ventas-container">
        {/* Encabezado */}
        <header className="ventas-header mb-6">
          <h1 className="text-2xl font-bold text-gray-800">🏬 Gestión de Almacén</h1>
          <p className="text-sm text-gray-500">Controla stock, movimientos y alertas del almacén.</p>
        </header>

        {/* Navegación de vistas */}
        <nav className="flex gap-4 mb-6">
          <button
            className={`modal-trigger-btn ${vistaActiva === "alerta" ? "active" : ""}`}
            onClick={() => cambiarVista("alerta")}
          >
            🚨 Alerta de Stock
          </button>
          <button
            className={`modal-trigger-btn ${vistaActiva === "movimientos" ? "active" : ""}`}
            onClick={() => cambiarVista("movimientos")}
          >
            🔄 Entradas/Salidas
          </button>
          <button
            className={`modal-trigger-btn ${vistaActiva === "kardex" ? "active" : ""}`}
            onClick={() => cambiarVista("kardex")}
          >
            📜 Kardex
          </button>
        </nav>

        {/* Contenido dinámico */}
        <section>
          {vistaActiva === "alerta" && <AlertaStock />}
          {vistaActiva === "movimientos" && <EntradasSalidas />}
          {vistaActiva === "kardex" && <KardexHistorial />}
        </section>
      </div>
    </main>
  );
}
